import React, { useContext } from "react";
import "./Statistics.scss";
import StyleContext from "../../contexts/StyleContext";

const data = [
  {heading: "1200+", content: "Hours of Full Stack Coding"},
  {heading: "200+", content: "Git Commits"},
  {heading: "100+", content: "Hours of Soft Skill Sessions"},
  {heading: "5+", content: "Project Done"}
];

const Statistics = () => {
    const {isDark} = useContext(StyleContext);

  return (
    <div style={{marginTop: "100px",marginBottom:"340px"}}>
      <div className="heading">
        Statistics
        <div className={isDark
                ? "data-div-dark"
                : "data-div-light"}>
          {data.map((item,i) => (
            <div key={i} style={{display:"flex", flexDirection:"column", gap:"10px"}}>
              <div className="data-heading">{item.heading}</div>
              <div className="data-content">{item.content}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Statistics;
